<template>
  <div class="featureExtraction">
    <div class="top-content content-border">
      <div class="content-title">
        <div class="content-title-left">
          <span class="block"></span>
          <span>低压客户停电情况</span>
        </div>
      </div>
      <div class="chart-area">
        <div class="process">
          <span class="process-tit1">停电后投诉</span>
          <div class="process-content">
            <span class="tsYes" :style="{ width: complainNumber + '%' }"></span>
            <span class="tsNo" :style="{ width: uncomplainNumber + '%' }"></span>
          </div>
          <span class="process-tit2">停电后不投诉</span>
        </div>
        <el-row :gutter="10">
          <el-col :span="12">
            <div class="content content-border">
              <div class="content-title">
                <div class="content-title-left">
                  <span class="block"></span>
                  <span>用电类别分布</span>
                </div>
              </div>
              <div class="content-main content-boder">
                <pieCharts
                  v-if="powerCategoryUsersRes"
                  :pieChartsData="powerCategoryUsersData"
                  :roseType="false"
                  :labelDw="'%'"
                  :radius="['30%', '70%']"
                  :name="'用电类别分布'"
                  @setOption="setOption"
                ></pieCharts>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="content content-border">
              <div class="content-title">
                <div class="content-title-left">
                  <span class="block"></span>
                  <span>95598来电次数</span>
                </div>
              </div>
              <div class="content-main content-boder">
                <pieCharts
                  v-if="callTimesRes"
                  :pieChartsData="callTimesData"
                  :roseType="false"
                  :labelDw="'%'"
                  :minAngle="45"
                  :radius="['30%', '70%']"
                  :name="'95598来电次数'"
                  @setOption="setOption"
                ></pieCharts>
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="12">
            <div class="content content-border">
              <div class="content-title">
                <div class="content-title-left">
                  <span class="block"></span>
                  <span>停电次数</span>
                </div>
              </div>
              <div class="content-main content-boder">
                <barCharts
                  v-if="outageTimeRes"
                  :barChartsData="outageTimeData"
                  :barWidth="40"
                  :barType="'bar'"
                  :showDetail="true"
                  :name="'停电次数'"
                  @setOption="setOption"
                ></barCharts>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="content content-border">
              <div class="content-title">
                <div class="content-title-left">
                  <span class="block"></span>
                  <span>停电间隔时长</span>
                </div>
              </div>
              <div class="content-main content-boder">
                <barCharts
                  v-if="outageIntervalRes"
                  :barChartsData="outageIntervalData"
                  :barWidth="40"
                  :barType="'bar'"
                  :name="'停电间隔时长'"
                  @setOption="setOption"
                ></barCharts>
              </div>
            </div>
          </el-col>
        </el-row>
        <div class="info content-border">
          <div class="info-header">
            <span class="th">特征</span>
            <span
              class="info-text"
              v-for="(item, index) in featureWeightData"
              :key="index"
            >{{ item.name }}</span>
          </div>
          <div class="info-content">
            <span class="th">权重系数</span>
            <span class="info-text" v-for="(item, index) in featureWeightData" :key="index">
              <div class="info-text-main">{{ item.value }}</div>
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-content content-border">
      <div class="content-title">
        <div class="content-title-left">
          <span class="block"></span>
          <span>用户明细列表</span>
        </div>
        <div class="content-title-right">
          <el-input
            placeholder="请输入用户编号"
            size="mini"
            class="input"
            v-model="userDetailQueryForm.userNum"
            clearable
          ></el-input>
          <el-button type="primary" size="mini" @click="getUserDetailData">搜索</el-button>
        </div>
      </div>
      <div class="content-main">
        <bdaTable
          class="main-table"
          :tableData="userDetailData"
          :headerColor="'#1899fa'"
          :isPage="true"
          :pageConfig="userDetailPageConfig"
          @changePage="changePage"
        ></bdaTable>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/power-sensitive-users';
import bdaTable from "../../components/bda-table/bda-table";
import barCharts from "../../components/bar-charts/bar-charts";
import pieCharts from "../../components/pie-charts/pie-charts";

export default {
  name: "featureExtraction",
  components: {
    bdaTable,
    barCharts,
    pieCharts
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      complainNumber: 0,
      uncomplainNumber: 0,
      headerColor: "#1899fa",
      //用户类别
      powerCategoryUsersData: {},
      powerCategoryUsersRes: true,
      //来电次数
      callTimesRes: false,
      callTimesData: {},
      //停电次数
      outageTimeRes: false,
      outageTimeData: {},
      //停电间隔时长
      outageIntervalRes: false,
      outageIntervalData: {},
      //特征权重
      featureWeightData: [
        { name: "用电类别", value: 0 },
        { name: "95598来电次数", value: 0 },
        { name: "停电次数", value: 0 },
        { name: "停电间隔时长", value: 0 }
      ],
      //用电列表详细
      userDetailData: {},
      userDetailQueryForm: {
        userNum: "",
        current: 1,
        size: 10
      },
      //分页设置
      userDetailPageConfig: {
        name: "userDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //图表组件数据
      setOptionData: {}
    };
  },
  mounted() {
    this.getComplainData()
    this.getPowerCategoryUsersData();
    this.getCallTimesData();
    this.getOutageTimeData();
    this.getOutageIntervalData();
    this.getFeatureWeightData()
    this.getUserDetailData();
    window.addEventListener("resize", () => {
      if (this.setOptionData["用电类别分布"])
        this.setOptionData["用电类别分布"].resize();
      if (this.setOptionData["95598来电次数"])
        this.setOptionData["95598来电次数"].resize();
      if (this.setOptionData["停电次数"])
        this.setOptionData["停电次数"].resize();
      if (this.setOptionData["停电间隔时长"])
        this.setOptionData["停电间隔时长"].resize();
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val;
    },
    changePage(data) {
      console.log(data, "data");
      if (data.name === "userDetail") {
        this.userDetailQueryForm.size = data.size;
        this.userDetailQueryForm.current = data.current;
        this.getUserDetailData();
      }
    },
    //获取停电投诉,不投诉
    getComplainData() {
      this.complaintsPowerFailure({}).then(res => {
        this.complainNumber = res[1].count
        this.uncomplainNumber = res[0].count
      })
    },
    // 用户类别
    getPowerCategoryUsersData() {
      this.powerCategoryUsersRes = false;
      this.categoryDistribution({}).then(res => {
        let dataMap = []
        dataMap = res.map(e => {
          return {
            name: e.YDLX,
            value: e.COUNTS,
          }
        })
        this.powerCategoryUsersData = {
          dataMap: dataMap,
          colorMap: ["#1A84F4", "#08BEF3", "#1DE0BF", "#EAA01A"]
        };
        this.powerCategoryUsersRes = true;
      })
    },
    //来电次数
    getCallTimesData() {
      this.callTimesRes = false;
      this.callNumber({}).then(res => {
        let dataMap = []
        dataMap = res.map(e => {
          return {
            name: e.TDTSCS,
            value: e.COUNTS,
          }
        })
        this.callTimesData = {
          dataMap: dataMap,
          colorMap: ["#1A84F4", "#08BEF3", "#1DE0BF", '#42ce50', "#EAA01A"]
        };
        this.callTimesRes = true;
      })
    },
    // 停电次数
    getOutageTimeData() {
      this.outageTimeRes = false;
      this.silentNumber({}).then(res => {
        let dataMap = []
        let xNameData = []
        res.forEach(e => {
          dataMap.push(e.COUNTS)
          xNameData.push(e.TDCS)
        });
        this.outageTimeData = {
          dataMap: {
            '停电次数': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '停电次数': [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF"],
              ["#19A7CD", "#66D6FF"],
              ["#19CDAE", "#77E7BE"],
              ["#29C644", "#8AE671"],
              ["#CC8A00", "#FACE8A"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#1A84F4", diffcolor: true },
            y轴: [{ name: "客户数", color: "#808080" }]
          }
        };
        this.outageTimeRes = true;
      })
    },
    // 停电间隔时长
    getOutageIntervalData() {
      this.outageIntervalRes = false;
      this.durationOutage({}).then(res => {
        let dataMap = []
        let xNameData = []
        res.forEach(e => {
          dataMap.push(e.COUNTS)
          xNameData.push(e.TDJGSC)
        });
        this.outageIntervalData = {
          dataMap: {
            '停电间隔时长': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '停电间隔时长': [
              ["#2790FF", "#55BFFF"],
              ["#19A7CD", "#66D6FF"],
              ["#19CDAE", "#77E7BE"],
              ["#29C644", "#8AE671"],
              ["#CC8A00", "#FACE8A"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#1A84F4", diffcolor: true },
            y轴: [{ name: "客户数", color: "#808080" }]
          }
        };
        this.outageIntervalRes = true;
      })

    },
    //获取特征权重
    getFeatureWeightData() {
      this.weightRatio({}).then(res => {
        this.featureWeightData = [
          { name: "用电类别", value: res.YDLB },
          { name: "95598来电次数", value: res.LDCS },
          { name: "停电次数", value: res.TDCS },
          { name: "停电间隔时长", value: res.TDJGSC }
        ]
      })
    },
    //获取用电详细列表
    getUserDetailData() {
      this.userDetailData = {};
      if (this.userDetailQueryForm.userNum) {
        this.userDetailQueryForm.current = 1
        this.userDetailQueryForm.size = 10
      }
      this.getUserInfo(this.userDetailQueryForm).then(res => {
        console.log(res, 'res')
        this.userDetailPageConfig.total = res.total;
        this.userDetailPageConfig.size = this.userDetailQueryForm.size;
        this.userDetailPageConfig.current = this.userDetailQueryForm.current;
        this.userDetailData = {
          tableData: [...res.records],
          columnNameData: [
            { prop: "userNum", label: "用户编号" },
            { prop: "userName", label: "用户名称" },
            { prop: "userAdress", label: "用户地址" },
            { prop: "adress", label: "地区" },
            { prop: "proviceCur", label: "供电所" },
            { prop: "tagUpdateTime", label: "标签更新时间" },
            { prop: "userType", label: "用户类型" },
            { prop: "cpTimes", label: "停电用户投诉数" },
            { prop: "curInterval", label: "停电时间间隔" },
            { prop: "curTimes", label: "停电次数" },
            { prop: "ssLevel", label: "停电敏感等级" }
          ]
        };
      });
    }
  }
};
</script>

<style scoped lang="scss">
.featureExtraction {
  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .top-content,
  .bottom-content {
    margin-bottom: 20px;
    height: 100%;
    .content-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 20px;
      line-height: 20px;
      padding: 5px;
      font-weight: bold;
      color: #1a84f4;

      .block {
        display: inline-block;
        width: 8px;
        height: 16px;
        margin: 0 10px 0 0;
        background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
      }

      .content-title-left {
        display: flex;
        align-items: center;
        margin-left: 10px;
      }
    }
    .content {
      height: 300px;
      margin: 10px 0px 0px;
      .content-main {
        height: calc(100% - 30px);
      }
    }
  }
  .top-content {
    .chart-area {
      ::v-deep .el-table .warning-row {
        background: #e9f3fe;
      }
      line-height: 30px;
      padding: 10px;
      box-sizing: border-box;

      .process {
        display: flex;
        align-items: center;
        width: 100%;
        height: 30px;
        .process-tit1 {
          width: 80px;
          color: #1899fa;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .process-tit2 {
          width: 100px;
          color: #ff8000;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .process-content {
          width: calc(100% - 180px);
          height: 100%;
          display: flex;
          align-items: center;
          .tsYes {
            background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
            height: 16px;
            border-top-left-radius: 6px;
            border-bottom-left-radius: 6px;
          }

          .tsNo {
            background-color: #ff8000;
            height: 16px;
            border-top-right-radius: 6px;
            border-bottom-right-radius: 6px;
          }
        }
      }
      .info {
        height: 150px;
        margin: 10px 0;
        padding: 0 10px;
        .th {
          width: 80px;
        }
        .info-header {
          height: 50%;
          display: flex;
          align-items: center;
          border-bottom: 2px solid #eee;
          .info-text {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
        .info-content {
          height: 50%;
          display: flex;
          align-items: center;
          .info-text {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            .info-text-main {
              display: flex;
              align-items: center;
              justify-content: center;
              width: 70%;
              border-radius: 4px;
              background-color: #e5f2ff;
            }
          }
        }
      }
    }
  }
  .bottom-content {
    height: 400px;
    .content-title {
      height: 40px;
      .content-title-right {
        display: flex;
        margin-right: 20px;
      }
    }
    .content-main {
      height: calc(100% - 50px);
      padding: 10px;
      box-sizing: border-box;
    }
  }
}
</style>
